
.currency-chart {
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  height: 100%;
  cursor: crosshair;
}
.currency-chart svg {
  height: 100%;
  width: 100%;
}
.view {
  fill: rgba(255,255,255,0.2);
}
.axis path {
  display: none;
}
.axis line {
  stroke-opacity: 0.3;
}
.line {
  fill: none;
  stroke: #4682b4;
  stroke-width: 1.5px;
}
button {
  position: absolute;
  top: 20px;
  left: 80px;
}


